.categories {
    margin: 80px auto;
}

.categories-container {
    margin: 50px auto;
    display: flex;
    justify-content: space-around;
    column-gap: 20px;
}

.best-selling-card {
    color: #343434;
    text-transform: uppercase;
    font-family: "Open Sans", sans-serif;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 4px;
    text-align: center;
}

.category-picture {
    width: 150px;
    height: 150px;
    border-radius: 20px;
    background-size: cover;
    margin: 20px 0;
    box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.1);
}